
.cart {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    width: 1300px;
    height: 550px;
    text-align: center;
    align-items: center;
    border: 1px solid;
}

.cart-icon-and-text {
  display: inline-flex;
  gap: 20px;
}

.cart-organization {
  display: flex;
  flex-direction:row;
  height: 455px;
  width: 1150px;
  margin: 20px;
  text-align: center;
  padding: 20px;
  border: 1px solid;
}

.items-scrollpane {
  overflow: auto;
  height: 400px;
  width: 800px;
  margin: 20px;
  text-align: center;
  padding: 20px;
  border: 1px solid;
}

.items-list p {
  padding: 10px;
  margin: 20px 0;
  border: 2px solid;
}

.payment-checkout-pane {
  display: flex;
  flex-direction: column;
  text-align: right;
  width: 400px;
  margin-top: 70px;
  gap: 50px;
}

.payment-checkout-pane a {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: right;
  text-decoration: none;
}

.payment-pane {
  border: 2px solid;
}

.checkout {
  background-color: cornflowerblue;
  display: flex;
  width: 300px;
  cursor: pointer;
  border-radius: 8px;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  height: 60px;
}